import React, { Component, useState, useEffect } from 'react'
import axios from 'axios'
import './print.css'
import Labels from './Labels'

class Previews extends Component {

    // }= ({ cid, order }) => {
    // const componentRef = useRef();
    // const [tagInfo, getTagInfo] = useState([])
    // console.log(order)

    constructor(props) {
        super(props)
        this.state = {
            tagInfo: []
        }
    }

    componentDidMount() {
        axios.get(`http://sv8/api/additional/getLabelsInfo/${this.props.cid}/${this.props.order}`)
            .then(response => {
                console.log(response.data + ' Did ')
                this.setState({ tagInfo: response.data })
            })
    }

    render() {
        return (
            <>
                {this.state.tagInfo.map((val) => {
                    return <Labels props={val} />
                })
                }
            </>
        )
    }
}

// Print.propTypes = {
